body {
  background-color: #F5F5F5;
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: center;
  font-family: 'PT Sans', sans-serif; }

.tags {
  padding-top: 25px;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  h1 {
    font-weight: normal;
    font-size: 14px;
    color: #5C5C5C;
    margin-top: 0; }
  h2 {
    font-size: 16px;
    color: #5C5C5C;
    margin-top: 30px; }
  &__type {
    display: flex;
    margin-top: 80px;
    margin-bottom: 50px;
    font-size: 12px;
    color: #969696;
    text-transform: uppercase;
    span {
      margin-right: 40px;
      cursor: pointer;
      display: flex;
      align-items: center; }

    span.active {
      color: #0077C2; }
    svg {
      margin-right: 8px; } }

  &__list {
    display: flex;
    flex-wrap: wrap;
    width: 810px; }
  &__list-count {
    position: absolute;
    background-color: #42A5F5;
    border-radius: 50%;
    color: #fff;
    top: 25px;
    right: -35px;
    width: 16px;
    padding-top: 1px;
    text-align: center;
    font-size: 12px; }
  &__list-expand {
    position: absolute;
    right: -25px; }
  &__list-remove {
    position: absolute;
    right: 15px; }
  &__list-item {
    border-radius: 32px;
    position: relative;
    padding: 11px 38px;
    font-size: 14px;
    cursor: pointer;
    margin-bottom: 32px;
    margin-right: 55px; }
  &__list-item--genre {
    border: 2px solid #42A5F5;
    color: #42A5F5; }
  &__list-item--genre--active {
    background-color: #42A5F5;
    border: 2px solid #42A5F5;
    color: #fff; }
  &__list-item--mood {
    border: 2px solid #EC407A;
    color: #EC407A; }
  &__list-item--mood--active {
    background-color: #EC407A;
    border: 2px solid #EC407A;
    color: #fff; }
  &__choosed {
    width: 810px;
    font-size: 14px; }
  &__choosed-title {
    font-size: 12px;
    letter-spacing: 0.08em;
    line-height: 20px;
    text-transform: uppercase;
    color: #5C5C5C;
    margin-right: 30px; }
  &__choosed-item {
    display: inline-block;
    margin-right: 35px;
    margin-bottom: 15px;
    &:last-child {
      margin-right: 0; } }
  &__choosed-item--genre {
    color: #42A5F5; }
  &__choosed-item--mood {
    color: #EC407A; }
  &__choosed-remove {
    cursor: pointer;
    margin-left: 10px; }
  &__songs-count {
    font-size: 16px;
    color: #5C5C5C; }
  &__songs-title {
    color: #5C5C5C;
    font-size: 14px;
    margin-top: 40px;
    margin-bottom: 10px; }
  &__songs {
    display: flex;
    flex-direction: column;
    align-items: center; }
  &__btn {
    border-radius: 100px;
    color: #fff;
    text-transform: uppercase;
    padding: 10px 25px;
    margin-top: 30px;
    cursor: pointer;
    background-color: #15BF6D;
    &:focus {
      outline: none; } }
  &__child-info {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #42A5F5;
    margin-bottom: 40px; }
  &__child-back {
    margin-right: 32px;
    cursor: pointer; } }




